.is-absolute {
  position: absolute;
}

.app-spinner {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #989898;
  height: 100%;
  width: 100%;
  min-width: 25px;
  min-height: 25px;
  position: absolute;
  top: 0;
  z-index: 999999;
  visibility: visible;
  opacity: 1;
  transition: visibility 0.5s, opacity 0.3s linear;
  
}

