.hide {
  visibility: hidden;
  opacity: 0;
}


.spinner-wrapper {
  position: fixed;
  z-index: 9999;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.08);
  font-size: 50px;
  color: white;
  transform: translate(-50%, -50%);
  
  
 
}

.is-small spinner-img{
    
  max-width: 32px;

}
@keyframes loading-text-opacity {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
